/**
 * Program block editor adjustments
 *
 * @since   1.0.0
 * @package Full_Score_Events
 */
.wp-block-full-score-events-program-edit {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  border: 2px solid var(--global--color-border); }
  .wp-block-full-score-events-program-edit > .block-editor-inner-blocks > .block-editor-block-list__layout {
    display: grid;
    gap: 1rem; }
  @media (min-width: 50rem) {
    .wp-block-full-score-events-program-edit {
      padding-top: 2rem;
      padding-bottom: 2rem; } }

.fse-piece-arranger-name {
  display: inline-block;
  min-width: 0.5rem; }

.editor-styles-wrapper .fse-program-heading-heading,
.editor-styles-wrapper .fse-program-subheading,
.editor-styles-wrapper .fse-program-tertiary-heading,
.editor-styles-wrapper .fse-piece-title,
.editor-styles-wrapper .fse-piece-composer,
.editor-styles-wrapper .wp-block-full-score-events-program-edit .wp-block,
.editor-styles-wrapper .fse-program-pieces {
  margin: 0; }

.editor-styles-wrapper .wp-block-full-score-events-program-heading,
.editor-styles-wrapper .wp-block-full-score-events-program-pieces,
.editor-styles-wrapper .fse-program-pieces {
  padding-left: 1.5rem;
  padding-right: 1.5rem; }

@media (min-width: 50rem) {
  .editor-styles-wrapper .wp-block-full-score-events-program-heading,
  .editor-styles-wrapper .wp-block-full-score-events-program-pieces,
  .editor-styles-wrapper .fse-program-pieces {
    padding-left: 2rem;
    padding-right: 2rem; } }
